.main-wrapper {
  border-radius: 10px;
  background: rgb(241, 241, 241);
  min-height: 150px;
  margin-top: 20px;
  box-shadow: 0px 0px 7px rgba($color: #000000, $alpha: 0.1);
  overflow: hidden;
}

.container-title {
  display: flex;
  justify-content: center;
  position: relative;
}

h4 {
  padding: 5px;
  background: rgb(235, 235, 235);
  border-bottom: 1px solid rgba(128, 128, 128, 0.26);
  text-align: center;
  width: 100%;
}

.content {
  margin: 20px;
}

@media only screen and (max-width: 992px) {
  h4 {
    padding-left: 21px;
    font-size: 18px !important;
  }
  .content {
    margin: 5px;
  }
}
